import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent {

  lbresearch: boolean = true;
  edinburgh: boolean = false;
  paris: boolean = false;
  blog: boolean = false;
  contact: boolean = false;

  onNavigated (entry: string) {
    switch(entry) {
      case "lbresearch":
        this.lbresearch = true;
        this.edinburgh = false;
        this.paris = false;
        this.blog = false;
        this.contact = false;
        break;
      case "edinburgh":
        this.lbresearch = false;
        this.edinburgh = true;
        this.paris = false;
        this.blog = false;
        this.contact = false;
        break;
      case "paris":
        this.lbresearch = false;
        this.edinburgh = false;
        this.paris = true;
        this.blog = false;
        this.contact = false;
        break;
      case "contact":
        this.lbresearch = false;
        this.edinburgh = false;
        this.paris = false;
        this.blog = false;
        this.contact = true;
        break;
      case "blog":
        this.lbresearch = false;
        this.edinburgh = false;
        this.paris = false;
        this.blog = true;
        this.contact = false;
        break;
      default:
        this.lbresearch = true;
        this.edinburgh = false;
        this.paris = false;
        this.blog = false;
        this.contact = false;
    }
  }
}
