import { Component, EventEmitter, Output, OnInit } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
  selector: 'app-navigation',
  animations: [
    trigger('openClose', [
      state('open', style({
        right: '0',
        opacity: 1
      })),
      state('closed', style({
        right: '200px',
        opacity: 0
      })),
      transition('open => closed', [
        animate('0.5s')
      ]),
      transition('closed => open', [
        animate('0.5s')
      ]),
    ]),
  ],
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.less']
})
export class NavigationComponent implements OnInit {

  lbresearchIsOpen = true;
  edinburghIsOpen = false;
  parisIsOpen = false;
  blogIsOpen = false;
  contactIsOpen = false;

  @Output() navigated = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {
  }

  navigationClicked(entry: string) {
    this.navigated.emit(entry);
    switch(entry) {
      case "lbresearch":
        this.lbresearchIsOpen = true;
        this.edinburghIsOpen = false;
        this.parisIsOpen = false;
        this.blogIsOpen = false;
        this.contactIsOpen = false;
        break;
      case "edinburgh":
        this.lbresearchIsOpen = false;
        this.edinburghIsOpen = true;
        this.parisIsOpen = false;
        this.blogIsOpen = false;
        this.contactIsOpen = false;
        break;
      case "paris":
        this.lbresearchIsOpen = false;
        this.edinburghIsOpen = false;
        this.parisIsOpen = true;
        this.blogIsOpen = false;
        this.contactIsOpen = false;
        break;
      case "blog":
        this.lbresearchIsOpen = false;
        this.edinburghIsOpen = false;
        this.parisIsOpen = false;
        this.blogIsOpen = true;
        this.contactIsOpen = false;
        break;
      case "contact":
        this.lbresearchIsOpen = false;
        this.edinburghIsOpen = false;
        this.parisIsOpen = false;
        this.blogIsOpen = false;
        this.contactIsOpen = true;
        break;
      default:
        this.lbresearchIsOpen = true;
        this.edinburghIsOpen = false;
        this.parisIsOpen = false;
        this.blogIsOpen = false;
        this.contactIsOpen = false;
    }
  }
}
